import { Injectable } from '@angular/core';
import {Http,Response} from '@angular/http';
import {map} from 'rxjs/operators';
import {Observable} from 'rxjs';
import { RootObject } from './model/airport/airportjson';
import {CompleteFareDetail} from './model/faredetails/completeFareDetail';
@Injectable({
  providedIn: 'root'
})

export class KlmmockService {

  fareUrl="http://localhost:8082/travel/fare/";
  metricurl="http://localhost:8082/travel/metric";
  airportUrl="http://localhost:8082/travel/airports?size=50";
  airportModel:RootObject;
  completeFareDetail:CompleteFareDetail;  
  constructor(private _http:Http) { }
  public getAirportDetails(term:string):any
  {
    return this._http.get(this.airportUrl+"&term="+term).pipe(map(response=>{
      this.airportModel=response.json();
      return response.json();
    },
    error=>
    {
      this.handleError(error);
    }));
  }

  public getFare(origin:string,destination:string):any
  {
    return this._http.get(this.fareUrl+origin+"/"+destination).pipe(map(response=>{
      this.completeFareDetail=response.json();
    },
    error=>
    {
      this.handleError(error);
    }));
  }

  public getMetric():any
  {
    return this._http.get(this.metricurl).pipe(map(response=>{
    return response.json();
    },
    error=>
    {
      this.handleError(error);
    }));
  }

  public handleError(error)
  {
    console.error("Error is "+ error);
    return Observable.throw(error ||"Internal server error");
  }
}

