import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'requestcount'
})
export class RequestcountPipe implements PipeTransform {

  transform(requestmap: any): any {
    var data=0;
    console.log(">>>"+requestmap[0].url);
    for(var reqSize=1;reqSize<=requestmap.length;reqSize++)
    {
      data=reqSize;
    }
    return data;
  }

}
