import { Component, OnInit } from '@angular/core';
import {HttpErrorResponse} from '@angular/common/http';
import {KlmmockService} from '../../services/klmmock.service';

declare const displayRequestData:any;

@Component({
  selector: 'app-request-details',
  templateUrl: './request-details.component.html',
  styleUrls: ['./request-details.component.css']
})


export class RequestDetailsComponent implements OnInit {
  metricdata:any;
  requestStatus:any;
  hide:boolean;
  constructor(private _service:KlmmockService) { }

  ngOnInit() {
      this.hide=false;
      this._service.getMetric().subscribe(response=>{
      this.metricdata=response;
      this.requestStatus=this.metricdata.urldetail.requestMap;
      if(null!=this.requestStatus)
      {
        this.hide=true;
      }
      
    },
    (error:HttpErrorResponse)=>{
    if(error.error instanceof Error){
      console.log("Client side error");
    }
    else{
      console.log("Server side error");
        }
  });
}

 

}
